body {
  margin: 0px;
  font-weight: 300;
	font-family: 'Roboto';
  background-color: white;
}

.container {
  display: flex;
  min-height: 100vh;
  overflow-y: scroll;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.password-area {
  display: flex;
  padding: 0 0.5em;
  align-items: center;
  flex-direction: column;
  margin: 1em auto !important;
}

.password-text {
  font-size: 2em;
  padding: 0.5em 0;
  font-weight: 500;
  text-align: center;
  word-break: break-word;
}

.password-help { font-size: 0.8em; }
.hidden { display: none !important;  }
.container > div { margin: 0.5em auto; }

@media only screen and (min-width: 850px) {
  .container {
    margin: 0 auto;
    max-width: 800px;
    flex-direction: row;
  }

  .password-area { padding: 0 0em; }
  .container > div {
    max-width: 400px;
    margin: 0.5em auto;
  }
}