.control-group {
  margin: 0.75em 0;
  width: 20em;
  display: flex;
  flex-direction: column;
}

.control {
  padding: 10px;
  font-size: 1em;
  margin: 2px 0px;
  font-family: 'Roboto';
}

.control-label {
  color: #545454;
  margin: 2px 0px;
  font-size: 0.85em;
  font-weight: bolder;
}

::placeholder {
  font-weight: 300;
  font-size: 0.85em;
}

.radio-button-group {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.radio-button-group > .control-group {
  margin:  0.2em 0;
  align-items: center;
  flex-direction: row;
}

.checkbox-control-group,
.radio-control-group {
  display: flex;
  width: 20em;
  margin: 0.75em 0;
  align-items: center;
  flex-direction: row;
}

.checkbox-control-group > .control-label,
.radio-control-group > .control-label {
  color: black;
  font-weight: 300;
}

input[type="radio"],
input[type="checkbox"] { margin-right: 0.5em; }

input[type="button"] {
  color: white;
  font-size: 0.9em;
  font-weight: bolder;
  background-color: #03a9f4;
  border: 1px solid #015a83;
}

input[type="button"]:not:disabled:hover {
  background-color: #0279b1; 
  transition: 1s;
}

input {
  font-size: 1em;
  color: black;
}

input:disabled {
  cursor: not-allowed;
  background-color: darkgray;
}